<template>
  <div class="botupchat-footer-section" :style="UIPrefs.footer_section.css">
    <div class="botupchat-text-area-icons">
      <div class="botupchat-footer-icons" :style="UIPrefs.footer_icons.css">
        <b-form @submit.prevent="submit">
          <QuillEditor
            class="quill-editor d-flex flex-column-reverse"
            ref="quill_editor_ref"
            v-model="content"
            @input="onEditorChange"
            :options="editorOptions"
            @blur="editorBlur($event)"
            @focus="editorFocus($event)"
            @keypress.native="editorKeyPress($event)"
            @keydown.native="editorKeyDown($event)"
            :placeholder="placeholder"
          >
          </QuillEditor>

          <button class="botupchat-send-icon" :style="UIPrefs.send_icon.css" type="submit" v-if="add_button">
            <svg class="send-img" :style="UIPrefs.send_img.css" :disabled="button_disable || !content || content.length > max_length" viewBox="0 0 29 29" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M0 0L29 14.5L0 29L8.5 14.5L0 0Z" fill="#4E71EE" />
            </svg>
          </button>
        </b-form>
      </div>
    </div>
  </div>
</template>
<script>
  import "quill/dist/quill.snow.css";
  import "quill-emoji/dist/quill-emoji.css";
  import "quill-mention/dist/quill.mention.min.css";
  import QuillEmoji from "quill-emoji";
  import { quillEditor, Quill } from "vue-quill-editor";
  import QuillMention from "quill-mention";
  import Delta from "quill-delta";
  Quill.register("modules/mentions_config", QuillMention);
  Quill.register("modules/emoji-shortname", QuillEmoji.ShortNameEmoji);
  import QuillConfig from "../../js/window/quill-config";
  export default {
    props: {
      enableChat: Boolean,
      max_length: {
        type: Number,
        default: 1000, // Text validation
      },
      link_placeholder: {
        type: String,
        default: "https://500apps.com",
      },
    },
    components: { QuillEditor: quillEditor },
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
        placeholder: "Message",
        content: "",
        button_disable: false,
        add_button: true,
        // Quill editor config
        editorOptions: {
          modules: {
            toolbar: [...QuillConfig.toolbar],
            "emoji-toolbar": true,
            "emoji-textarea": true,
            "emoji-shortname": true,
            mentions_config: {
              allowedChars: /^[a-zA-Z0-9_]*$/,
              mentionDenotationChars: ["@"],
              dataAttributes: ["id", "name", "email"],
            },
            keyboard: {
              bindings: {
                enter: {
                  key: 13,
                  handler: function(range) {
                    this.quill.insertText(range.index, "\n"); // Add new line
                  },
                },
              },
            },
          },
          selectedFile: "",
        },
      };
    },
    watch: {},
    mounted() {
      this.$nextTick(() => {
        // change the link placeholder
        var tooltip = this.$refs.quill_editor_ref.quill.theme.tooltip;
        var input = tooltip.root.querySelector("input[data-link]");
        input.dataset.link = this.link_placeholder;
      });
    },
    methods: {
      /**
       * Event fires when user send message
       * @param {Object} message  Invoke message which is given by user
       */
      submit() {
        if (!this.content) return;
        window._Botup.State.commit("setActiveMessage", this.content);

        // To Remove existing text content in Quill editor
        if (document.getElementsByClassName("ql-editor")[0]) document.getElementsByClassName("ql-editor")[0].innerText = null;

        if (this.enableChat)
          this.$router.push({
            name: "chat",
            params: { active: true },
          });
      },

      // Emit value
      onEditorChange(content) {
        this.$emit("input", content);
      },
      // Emit on editor blur
      editorBlur(quill) {
        this.$emit("blur", quill);
      },
      // Emit on editor focus
      editorFocus(quill) {
        this.$emit("focus", quill);
      },
      // Emit on editor key press event
      editorKeyPress($event) {
        this.$emit("key_press", $event);
      },
      // Emit on key down event
      editorKeyDown($event) {
        if ($event.keyCode == 13 && !this.disable_enter) {
          return this.emit_key_down($event);
        }
        // If the event is only enter key, emmit enter event
        if ($event.keyCode == 13 && !$event.altKey && !$event.ctrlKey && !$event.shiftKey) {
          // As enter event adds new line - need to remove
          this.remove_last_character();
          // emit key_down event
          this.emit_key_down($event);
        }
      },
      emit_key_down(event) {
        this.submit();
        // Emit key_down event
        this.$emit("key_down", this.content);
      },
      remove_last_character() {
        let editor_ref = this.$refs.quill_editor_ref.quill;
        editor_ref.updateContents(
          // Create new delta object
          new Delta()
            // retain previous text
            .retain(editor_ref.getLength() - 1)
            // insert the string from params
            .delete(1),
        );
      },
    },
  };
</script>
<style>
  @import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap");
  body {
    font-family: "Poppins", sans-serif;
  }
  .ql-toolbar.ql-snow + .ql-container.ql-snow {
    border: unset;
    margin-top: 18px;
    margin-left: -12px;
  }
  .flex-column-reverse {
    width: 311px;
  }
  .botupchat-footer-section {
    border-bottom-left-radius: -4px;
    border-bottom-right-radius: -10px;
  }
  .botupchat-footer-section .botupchat-text-area-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
  }
  .botupchat-footer-section textarea {
    box-sizing: border-box;
    padding: 0px 50px 0px 0px;
    width: 100%;
    height: 100%;
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.33;
    background-color: rgb(247 247 248);
    white-space: pre-wrap;
    overflow-wrap: break-word;
    border: 0;
  }
  .botupchat-footer-section textarea:focus-visible {
    outline: none;
    border: 0;
  }
  button {
    background: none;
    border: unset;
  }

  .botupchat-footer-section .textarea-emoji-control {
    height: 17px;
    right: 65px;
    top: 64px;
    width: 17px;
    cursor: pointer;
  }
  .botupchat-footer-section .ql-editor.ql-blank::before {
    font-style: normal;
  }
  .botupchat-footer-section .ql-toolbar.ql-snow {
    border: 0px solid #ccc;
    box-sizing: border-box;
    padding: 0px;
    position: relative;
    bottom: -26px;
  }
  .botupchat-footer-section .ql-editor {
    max-height: 65px;
  }
  .botupchat-footer-section .ql-container {
    max-height: 35px;
    font-family: "Poppins", sans-serif;
  }
  .botupchat-footer-section .ql-snow.ql-toolbar button svg,
  .ql-snow .ql-toolbar button svg {
    width: 15px;
    height: 15px;
  }
  .botupchat-footer-section .ql-snow .ql-color-picker .ql-picker-label svg,
  .ql-snow .ql-icon-picker .ql-picker-label svg {
    width: 15px;
    height: 15px;
  }
  .margin-zero p {
    margin: 0;
  }
  .ql-editor::-webkit-scrollbar {
    width: 3px;
  }
  .ql-editor::-webkit-scrollbar-thumb {
    background: #d2d2d2;
    border-radius: 5px;
  }
  .ql-editor::-webkit-scrollbar-track {
    box-shadow: inset 20px 20px 20px #fff;
    border-radius: 0px;
  }
  .body-scroll::-webkit-scrollbar {
    width: 5px;
  }
  .body-scroll::-webkit-scrollbar-thumb {
    background: #d2d2d2;
    border-radius: 5px;
  }
  .body-scroll::-webkit-scrollbar-track {
    box-shadow: inset 20px 20px 20px #fff;
    border-radius: 0px;
  }
  .botupchat-footer-section .ql-toolbar.ql-snow .ql-formats {
    margin-right: 0px;
  }
  .botupchat-footer-section .ql-snow .ql-picker.ql-expanded .ql-picker-options {
    top: -103px;
  }
  .botupchat-footer-section .ql-snow .ql-color-picker .ql-picker-label,
  .ql-snow .ql-icon-picker .ql-picker-label {
    padding: 0px 4px;
  }
  .chat_icon .btn:focus,
  .btn.focus {
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgb(0 123 255 / 0%);
  }
  .botupchat-footer-section .ql-snow .ql-stroke {
    stroke: #aeabab;
    stroke-width: 1.5;
  }
  .botupchat-footer-section .ql-snow .ql-fill,
  .ql-snow .ql-stroke.ql-fill {
    fill: #aeabab;
  }
  .botupchat-btn-light:hover {
    background-color: #e4e5ff !important;
  }
  .botupchat-btn-light:focus,
  .botupchat-btn-light.focus {
    box-shadow: 0 0 0 0.2rem rgb(130 138 145 / 0%) !important;
  }
</style>



